
'use client';

import React, { useState, useEffect, useCallback } from 'react';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { Button } from '@/components/ui/button';
import { Command, CommandEmpty, CommandGroup, CommandInput, CommandItem, CommandList } from '@/components/ui/command';
import { Check, ChevronsUpDown, LoaderCircle } from 'lucide-react';
import { cn } from '@/lib/utils';
import { autocompletePlaces } from '@/lib/places-service';
import debounce from 'lodash.debounce';

interface PlaceAutocompleteProps {
  onPlaceSelect: (placeId: string, description: string) => void;
  location: { lat: number; lng: number } | null;
  types: string[];
}

interface Suggestion {
  placeId: string;
  description: string;
}

export function PlaceAutocomplete({ onPlaceSelect, location, types }: PlaceAutocompleteProps) {
  const [open, setOpen] = useState(false);
  const [inputValue, setInputValue] = useState('');
  const [suggestions, setSuggestions] = useState<Suggestion[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [selectedValue, setSelectedValue] = useState('');

  const fetchSuggestions = useCallback(
    debounce(async (query: string) => {
      if (query.length < 3) {
        setSuggestions([]);
        return;
      }
      setIsLoading(true);
      const result = await autocompletePlaces(query, location, types);
      if (result.success && result.data) {
        setSuggestions(result.data);
      } else {
        setSuggestions([]);
      }
      setIsLoading(false);
    }, 300),
    [location, types]
  );

  useEffect(() => {
    fetchSuggestions(inputValue);
  }, [inputValue, fetchSuggestions]);

  const handleSelect = (suggestion: Suggestion) => {
    setInputValue(suggestion.description);
    setSelectedValue(suggestion.description);
    onPlaceSelect(suggestion.placeId, suggestion.description);
    setOpen(false);
  };

  return (
    <Popover open={open} onOpenChange={setOpen}>
      <PopoverTrigger asChild>
        <Button
          variant="outline"
          role="combobox"
          aria-expanded={open}
          className="w-full justify-between h-10"
        >
          {selectedValue ? selectedValue : 'Search for a school...'}
          <ChevronsUpDown className="ml-2 h-4 w-4 shrink-0 opacity-50" />
        </Button>
      </PopoverTrigger>
      <PopoverContent className="w-[--radix-popover-trigger-width] p-0">
        <Command shouldFilter={false}>
          <CommandInput
            placeholder="Type to search..."
            value={inputValue}
            onValueChange={setInputValue}
          />
          <CommandList>
            {isLoading && (
              <div className='flex items-center justify-center p-2'>
                <LoaderCircle className='animate-spin w-5 h-5' />
              </div>
            )}
            {!isLoading && suggestions.length === 0 && inputValue.length > 2 && (
              <CommandEmpty>No places found.</CommandEmpty>
            )}
            <CommandGroup>
              {suggestions.map((suggestion) => (
                <CommandItem
                  key={suggestion.placeId}
                  value={suggestion.description}
                  onSelect={() => handleSelect(suggestion)}
                >
                  <Check
                    className={cn(
                      'mr-2 h-4 w-4',
                      selectedValue === suggestion.description ? 'opacity-100' : 'opacity-0'
                    )}
                  />
                  {suggestion.description}
                </CommandItem>
              ))}
            </CommandGroup>
          </CommandList>
        </Command>
      </PopoverContent>
    </Popover>
  );
}
