
'use server';

interface SchoolResult {
    id: string;
    displayName: {
        text: string;
    },
    formattedAddress: string;
    location: {
        latitude: number;
        longitude: number;
    }
}

interface AutocompletePrediction {
    place: string;
    placeId: string;
    text: {
        text: string;
        matches: {
            startOffset: number;
            endOffset: number;
        }[];
    };
}

interface PlaceDetails {
    name: string;
    address: string;
    location: {
        lat: number;
        lng: number;
    }
}

export async function autocompletePlaces(
  query: string,
  location: { lat: number, lng: number } | null,
  types: string[]
): Promise<{ success: boolean; data?: any[], error?: string }> {
  const apiKey = process.env.GOOGLE_MAPS_API_KEY;
  if (!apiKey) {
    return { success: false, error: "Google Maps API key is not configured on the server." };
  }
  
  const url = 'https://places.googleapis.com/v1/places:autocomplete';

  const headers = {
    'Content-Type': 'application/json',
    'X-Goog-Api-Key': apiKey,
  };

  const body = JSON.stringify({
    input: query,
    includedPrimaryTypes: types,
    ...(location && {
        locationBias: {
            circle: {
                center: {
                    latitude: location.lat,
                    longitude: location.lng,
                },
                radius: 10000.0, // 10km radius bias
            },
        }
    }),
  });

  try {
    const response = await fetch(url, { method: 'POST', headers, body });
    const data = await response.json();

    if (data.error || !data.suggestions) {
        return { success: false, error: `Places Autocomplete error: ${data.error?.message || 'No results'}` };
    }

    const predictions = data.suggestions.map((prediction: AutocompletePrediction) => ({
      placeId: prediction.placeId,
      description: prediction.text.text,
    }));
    
    return { success: true, data: predictions };
  } catch (e) {
    console.error("Error fetching autocomplete places:", e);
    return { success: false, error: 'An unknown error occurred while fetching autocomplete results.' };
  }
}

export async function getPlaceDetails(placeId: string): Promise<{ success: boolean, data?: PlaceDetails, error?: string }> {
     const apiKey = process.env.GOOGLE_MAPS_API_KEY;
    if (!apiKey) {
        return { success: false, error: "Google Maps API key is not configured on the server." };
    }
    
    const url = `https://places.googleapis.com/v1/places/${placeId}`;
    const headers = {
        'Content-Type': 'application/json',
        'X-Goog-Api-Key': apiKey,
        'X-Goog-FieldMask': 'displayName,formattedAddress,location'
    };
    
    try {
        const response = await fetch(url, { headers });
        const data = await response.json();
        
        if (data.error) {
             return { success: false, error: `Place Details error: ${data.error?.message || 'Not found'}` };
        }
        
        const details: PlaceDetails = {
            name: data.displayName.text,
            address: data.formattedAddress,
            location: {
                lat: data.location.latitude,
                lng: data.location.longitude,
            }
        };
        
        return { success: true, data: details };
    } catch(e) {
        console.error("Error fetching place details:", e);
        return { success: false, error: 'An unknown error occurred while fetching place details.' };
    }
}


export async function findNearbySchools(
  location: { lat: number, lng: number }
): Promise<{ success: boolean; data?: any[], error?: string }> {
  const apiKey = process.env.GOOGLE_MAPS_API_KEY;
  if (!apiKey) {
    return { success: false, error: "Google Maps API key is not configured on the server." };
  }
  
  const url = 'https://places.googleapis.com/v1/places:searchNearby';

  const headers = {
    'Content-Type': 'application/json',
    'X-Goog-Api-Key': apiKey,
    'X-Goog-FieldMask': 'places.id,places.displayName,places.formattedAddress,places.location',
  };

  const body = JSON.stringify({
    includedTypes: ["school", "primary_school", "secondary_school"],
    maxResultCount: 10,
    locationRestriction: {
      circle: {
        center: {
          latitude: location.lat,
          longitude: location.lng,
        },
        radius: 5000.0, // 5km radius
      },
    },
  });

  try {
    const response = await fetch(url, {
      method: 'POST',
      headers,
      body,
    });
    
    const data = await response.json();

    if (data.error || !data.places) {
        return { success: false, error: `Places API error: ${data.error?.message || 'No results'}` };
    }

    const schools = data.places.map((school: SchoolResult) => ({
      place_id: school.id,
      name: school.displayName.text,
      vicinity: school.formattedAddress,
      location: {
        lat: school.location.latitude,
        lng: school.location.longitude,
      }
    }));

    return { success: true, data: schools };
  } catch (e) {
    console.error("Error fetching nearby schools:", e);
    return { success: false, error: 'An unknown error occurred while fetching schools.' };
  }
}
